@use "variables";

.menu__items {
    list-style: none;
    margin: variables.$spacer;
}

.menu__item {
    background-color: variables.$c-secondary;
    color: variables.$c-primary;
    border: 1px solid variables.$c-secondary; 
    border-radius: variables.$rad-tiny;
}

.menu__item--active {
    color: variables.$c-secondary;
    background-color: variables.$c-primary;
    border: 1px solid variables.$c-secondary;
}

.menu__link {
    text-decoration: none;
	display: block;
	padding: 10px;
}

.menu__link:hover, 
.menu__link:focus {
	background-color: variables.$c-tertiary;
	color: variables.$c-secondary;
}

@media screen and (min-width: 600px) {
    .menu {
        margin-left: 20px;
    }

    .menu__items {

        display:flex;
        justify-content: flex-start;
        gap: 10px;
    }

    .menu__item {
        margin: variables.$spacer;
        display: inline-block;
    }

    .menu__link {
        padding: 10px 10px;
    }
}