@use "variables";

.header {
    width: 100%;
    padding: variables.$spacer;
    background-color: variables.$c-secondary;
    color: variables.$c-primary;
}

.header__logo {
    font-size: 50px;
    text-shadow: 0 3px 10px rgba(0,0,0,0.4);
    margin-top: 0;
}

.header__claim {
    color: variables.$c-primary;
    font-size: 20px;
    text-shadow: 0 3px 10px (variables.$c-tertiary);
        font-style: italic;
}

.header__subtitle {
    font-size: variables.$size_xl;
    font-style: bold;
    margin: 0;
}

.header__text {
    padding: 0;
    margin: 0 0 10px 0;
}

@media screen and (min-width: 600px) {
    .header__logo-claim,
    .header__info {
    display: flex;
    gap: 30px;
    align-items: baseline;
}

    .address,
    .opening-hours {
        display: flex;
        flex-direction: column;
    }
}