@use "variables";

.content__subtitle {
    color: variables.$c-primary;
    padding-bottom: 5px;
    margin: 0;
}

.content__text {
    text-align: justify;
}

.content__text--long {
    text-align: justify;
}

.content__unordered-list {
    margin: 0;
    padding: 15px;
    list-style-position: inside;
}

.content__unordered-list li {
    margin-bottom: 5px;
    padding: 0;
    text-align: justify;
}
